import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";

import { environment } from "../../../environments/environment";
import { Subject } from "rxjs";
import { map } from "rxjs/operators";
import { DatePipe } from "@angular/common";

@Injectable({ providedIn: "root" })
export class TransferStatusService {
    access_token = localStorage.getItem("token");
    headers = new HttpHeaders({
        "Content-Type": "application/json",
        Authorization: "Bearer " + this.access_token,
    });
    options = { headers: this.headers };
    transfers = new Subject<any>();
    transfer = new Subject<any>();

    constructor(private http: HttpClient, private datePipe: DatePipe) {}

    public getTransfersData() {
        return this.transfers.asObservable();
    }

    public getTransferData() {
        return this.transfer.asObservable();
    }

    get() {
        return this.http
            .get<any>(`${environment.apiUrl}/api/transfer`, this.options)
            .pipe(
                map((transfers) => {
                    this.transfers.next(transfers);
                    return transfers;
                })
            );
    }

    findById(transferId: any) {
        return this.http
            .get<any>(
                `${environment.apiUrl}/api/transfer/status/${transferId}`,
                this.options
            )
            .pipe(
                map((transfer) => {
                    this.transfer.next(transfer);
                    return transfer;
                })
            );
    }
}
