import { Component, OnInit } from "@angular/core";
import { routerTransition } from "../../../router.animations";
import { NgbActiveModal } from "@ng-bootstrap/ng-bootstrap";
import { TransferStatusService } from "src/app/shared/services/transfer-status.service";

@Component({
    selector: "app-transfer-info",
    templateUrl: "./info.component.html",
    styleUrls: ["./info.component.scss"],
    animations: [routerTransition()],
})
export class InfoComponent implements OnInit {
    transferId: any;
    transfer: any = {};

    constructor(
        public modal: NgbActiveModal,
        private transferStatusService: TransferStatusService
    ) {}

    ngOnInit() {
        this.transferStatusService
            .findById(this.transferId)
            .subscribe((data) => {
                this.transfer = data;
            });
    }

    close() {
        this.modal.close("Close click");
    }
}
