import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";

import { environment } from "../../../environments/environment";
import { Subject } from "rxjs";
import { map } from "rxjs/operators";

@Injectable({ providedIn: "root" })
export class EmployeeService {
    access_token = localStorage.getItem("token");
    headers = new HttpHeaders({
        "Content-Type": "application/json",
        Authorization: "Bearer " + this.access_token,
    });
    options = { headers: this.headers };
    employees = new Subject<any>();
    employee = new Subject<any>();

    constructor(private http: HttpClient) {}

    public getEmployeesData() {
        return this.employees.asObservable();
    }

    public getEmployeeData() {
        return this.employee.asObservable();
    }

    get() {
        return this.http
            .get<any>(`${environment.apiUrl}/api/employee`, this.options)
            .pipe(
                map((employees) => {
                    this.employees.next(employees);
                    return employees;
                })
            );
    }

    findById(employeeId: any) {
        return this.http
            .get<any>(
                `${environment.apiUrl}/api/employee/${employeeId}`,
                this.options
            )
            .pipe(
                map((employee) => {
                    this.employee.next(employee);
                    return employee;
                })
            );
    }

    delete(employeeId: any) {
        return this.http
            .delete<any>(
                `${environment.apiUrl}/api/employee/${employeeId}`,
                this.options
            )
            .pipe(
                map((response) => {
                    return response;
                })
            );
    }

    add(data: any) {
        const joined_on = `${data.joined_on.year}-${data.joined_on.month}-${data.joined_on.day}`;
        const body = {
            firstname: data.firstname,
            lastname: data.lastname,
            email: data.email,
            phone: data.phone,
            acc_no: data.acc_no,
            ifsc_code: data.ifsc_code,
            joined_on: joined_on,
            salary: data.salary,
            number: data.number,
        };
        return this.http
            .post<any>(
                `${environment.apiUrl}/api/employee/create`,
                body,
                this.options
            )
            .pipe(
                map((response) => {
                    return response;
                })
            );
    }

    update(data: any, employeeId: any) {
        const joined_on = `${data.joined_on.year}-${data.joined_on.month}-${data.joined_on.day}`;
        const body = {
            firstname: data.firstname,
            lastname: data.lastname,
            email: data.email,
            phone: data.phone,
            acc_no: data.acc_no,
            ifsc_code: data.ifsc_code,
            joined_on: joined_on,
            salary: data.salary,
            number: data.number,
        };
        return this.http
            .post<any>(
                `${environment.apiUrl}/api/employee/update/${employeeId}`,
                body,
                this.options
            )
            .pipe(
                map((response) => {
                    return response;
                })
            );
    }
}
